.smart-timeline {
  position: relative; }

.smart-timeline-list:after {
  content: " ";
  background-color: #eee;
  position: absolute;
  display: block;
  width: 2px;
  top: 0;
  left: 95px;
  bottom: 0;
  z-index: 1; }

.smart-timeline-list {
  list-style: none;
  margin: 0;
  padding: 0; }
  .smart-timeline-list li {
    position: relative;
    margin: 0;
    padding: 15px 0; }
  .smart-timeline-list > li:hover {
    background-color: #f4f4f4; }

.smart-timeline-hover li:hover {
  background-color: #f9f9f9; }

.smart-timeline-icon {
  background: #3276B1;
  color: #fff;
  border-radius: 50%;
  position: absolute;
  width: 32px;
  height: 32px;
  line-height: 28px;
  font-size: 1.07692rem;
  text-align: center;
  left: 80px;
  top: 10px;
  z-index: 100;
  padding: 2px; }
  .smart-timeline-icon > img {
    height: 32px;
    width: 32px;
    max-width: none;
    border-radius: 50%;
    margin-top: -2px;
    margin-left: -2px;
    border: 2px solid #3276B1; }

.smart-timeline-time {
  float: left;
  width: 70px;
  text-align: right; }
  .smart-timeline-time > small {
    font-style: italic; }

.smart-timeline-content {
  margin-left: 123px; }
